.container {
    display: flex;
    padding: 0;
    margin: 0;
    height: 100vh;
    background-color: #222C40;
    flex-direction: column;
    justify-content: space-around;
}

.card-container {
    height: 75%;
    position: relative;
    overflow: scroll;
}

h3 {
    color: #ffffff;
    margin: 0;
    padding: 0 20px 12px;
    font-size: 18px;
}

.button-container {
    display: flex;
    flex-direction: row;
    padding: 0 5px;
    justify-content: center;
}

button {
    align-items: center;
    margin: 10px;
    width: 50%;
    justify-content: center;
    padding: 16px 20px;
    border: none;
    border-radius: 20px;
    color: #222C40;
    font-weight: bold;
    font-size: 16px;
    background-color: #ffc048
}

button:disabled {
    border: solid 1px #828A97;
    color: white;
    box-shadow: rgb(253,103,70, 0) 0px 6px 12px;
    background-color: transparent;
}
