.card-next-sleep {
    background-color: #fff;
    border-radius: 20px;
    padding: 16px 24px;
    margin: 16px 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
}

.text-container-next-sleep {
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    flex-grow: 0;
    justify-content: center;
    align-items: flex-start;
}

.title {
    color: #404040;
    font-weight: 700;
    padding: 0;
    font-size: 18px;
    margin: 0 0 4px;
}

.accent {
    color: #77BFA3;
    font-weight: 900;
}

.future-sleep {
    color: #404040;
    padding: 0;
    font-weight: 600;
    margin: 0;
}

.look{
    color: #404040;
    font-size: 16px;
    padding: 0;
    font-weight: 600;
    margin: 0;
}

img {
    height: 80px;
    filter: invert(25%);
}

.invert {
    transform: scaleX(-1);
}