.card {
    background-color: #2e384e;
    border-radius: 20px;
    padding: 16px 24px;
    margin: 16px 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.text-container {
    display: flex;
    flex-direction: column;

}

.previous-sleep {
    color: #A9AAB0;
    padding: 0;
    font-weight: bold;
    margin: 0px 0;
}

.time-stamp {
    font-size: 14px;
    color: rgb(115,115,115);
    font-weight: bold;
    padding-top: 4px;
    margin: 0;
}


.card-button {
    width: 40px;
    height: 30px;
    background-color: transparent;
    color: #222C40;
    margin: 0 23px 0 0;
    padding: 0;
}

 .material-symbols-outlined {
     /*line-height: 1.25;*/
     font-size: 30px;
     color: #EF4B4C;
     font-variation-settings:
             'FILL' 0,
             'wght' 400,
             'GRAD' 0,
             'opsz' 48
 }

